import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {CharacterService} from "../../services/character.service";
import {SettingService} from "../../services/setting.service";
import {map, shareReplay, tap} from "rxjs/operators";
import Skill from "../../model/setting/skill";
import {combineLatest, Observable} from "rxjs";
import SkillLevel from "../../model/character/skill-level";

@Component({
    selector: 'newera-skill-editor',
    templateUrl: './skill-editor.component.html',
    styleUrls: ['./skill-editor.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class SkillEditorComponent implements OnInit {

    public readonly skillsUnknown$ = this.characterService.skillsUnknown$.pipe(
        map(skills => skills.sort((skill1, skill2) =>
            skill1.name.localeCompare(skill2.name)))
    );
    public readonly skillLevels$ = this.characterService.skillLevels$;
    public readonly skillsLearnable$ = this.characterService.skillsLearnable$;

    public readonly skillLearningDisabled$ = combineLatest([
        this.skillLevels$,
        this.skillsLearnable$
    ]).pipe(
        map(([skillLevels, skillsLearnable]) => skillLevels.length >= skillsLearnable),
        tap(skillLearningDisabled => console.debug({skillLearningDisabled})),
        shareReplay()
    );

    public readonly skillSlots$: Observable<Array<SkillLevel | null>> = combineLatest([
        this.skillLevels$,
        this.skillsLearnable$
    ]).pipe(
        map(([skillLevels, skillsLearnable]) => {
            let slots: Array<SkillLevel | null>;
            if (skillLevels.length < skillsLearnable) {
                const placeholders = new Array<null>(skillsLearnable - skillLevels.length).fill(null);
                slots = [...skillLevels, ...placeholders];
            } else {
                slots = skillLevels;
            }
            return slots;
        }),
        tap(skillSlots => console.debug({skillSlots})),
        shareReplay()
    );

    public readonly skillPointsAvailable$ = this.characterService.skillsPointsAvailable$;
    public readonly skillPointsSpent$ = this.characterService.skillPointsSpent$;

    constructor(
        private readonly settingService: SettingService,
        private readonly characterService: CharacterService) {

    }

    ngOnInit(): void {
    }

    public learnSkill(skill: Skill) {
        this.characterService.learnSkill(skill);
    }

    public forgetSkill(skill: Skill) {
        this.characterService.unlearnSkill(skill);
    }

    public incrementSkillLevel(skillLevel: SkillLevel) {
        this.characterService.updateSkillLevel(
            new SkillLevel(skillLevel.skill, skillLevel.level + 1));
    }

    public decrementSkillLevel(skillLevel: SkillLevel) {
        this.characterService.updateSkillLevel(
            new SkillLevel(skillLevel.skill, skillLevel.level - 1));
    }
}
