import {Injectable} from '@angular/core';
import Character from "../model/character/character";
import {SettingService} from "./setting.service";
import PrimaryAttributeLevel from "../model/character/primary-attribute-level";
import {LevelService} from "./level.service";
import SecondaryAttributeLevel from "../model/character/secondary-attribute-level";
import Setting from "../model/setting/setting";
import SecondaryAttribute from "../model/setting/secondary-attribute";
import {combineLatest, Observable, Subject, Subscription} from "rxjs";
import {first, map, shareReplay, tap} from "rxjs/operators";
import Skill from "../model/setting/skill";
import SkillLevel from "../model/character/skill-level";
import Talent from "../model/setting/talent";

@Injectable({
    providedIn: 'root'
})
export class CharacterService {

    private readonly level$$ = new Subject<number>();
    public readonly level$ = this.level$$.asObservable().pipe(shareReplay());

    private readonly primaryAttributeLevels$$ = new Subject<PrimaryAttributeLevel[]>();
    public readonly primaryAttributeLevels$ = this.primaryAttributeLevels$$.asObservable().pipe(
        tap(primaryAttributeLevels => console.debug({primaryAttributeLevels})),
        shareReplay()
    );

    public readonly secondaryAttributeLevels$ = combineLatest([
        this.settingService.secondaryAttributes$,
        this.primaryAttributeLevels$
    ]).pipe(
        map(([secondaryAttributes, primaryAttributeLevels]) =>
            this.calculateSecondaryAttributeLevels(secondaryAttributes, primaryAttributeLevels)
        ),
        tap(secondaryAttributeLevels => console.debug({secondaryAttributeLevels})),
        shareReplay()
    );

    public readonly attributePointsAvailable$ = this.level$.pipe(
        map(level => this.levelService
            .getAvailableAttributePoints(level, true)),
        tap(attributePointsAvailable => console.debug({attributePointsAvailable})),
        shareReplay()
    );

    public readonly attributePointsSpent$ = this.primaryAttributeLevels$.pipe(
        map(attributeLevels => attributeLevels
            .map(attributeLevel => attributeLevel.level)
            .map(level => this.levelService.getCumulativeAttributeLevelCost(level))
            .reduce((total, value) => total + value)
        ),
        tap(attributePointsSpent => console.debug({attributePointsSpent})),
        shareReplay()
    );

    public readonly skillsLearnable$ = this.primaryAttributeLevels$.pipe(
        map(primaryAttributeLevels => primaryAttributeLevels.find(attributeLevel =>
            attributeLevel.attribute.id === "memory")!.level),
        tap(skillsLearnable => console.debug({skillsLearnable})),
        shareReplay()
    );

    public readonly skillsPointsAvailable$ = combineLatest([
        this.level$,
        this.secondaryAttributeLevels$
    ]).pipe(
        map(([characterLevel, secondaryAttributeLevels]) => {
            const insightLevel = secondaryAttributeLevels.find(attributeLevel =>
                attributeLevel.attribute.id === "insight")!.level;
            return this.levelService.getSkillsPointsAvailable(characterLevel, insightLevel);
        }),
        tap(skillsPointsAvailable => console.debug({skillsPointsAvailable})),
        shareReplay()
    );

    private readonly skillLevels$$ = new Subject<SkillLevel[]>();
    public readonly skillLevels$ = combineLatest([
        this.skillLevels$$,
        this.skillsLearnable$
    ]).pipe(
        map(([
                 skillLevels,
                 skillsLearnable
             ]) => {
            if (skillsLearnable < skillLevels.length) {
                return skillLevels.slice(0, skillsLearnable);
            }
            return skillLevels;
        }),
        tap(skillLevels => console.debug({skillLevels})),
        shareReplay()
    );

    public readonly skillsUnknown$ = combineLatest([
        this.settingService.skills$,
        this.skillLevels$
    ]).pipe(
        map(([skills, skillLevels]) => {
            const learnedSkillIds = new Set(skillLevels.map(sl => sl.skill.id));
            return skills.filter(skill => !learnedSkillIds.has(skill.id));
        }),
        tap(skillsUnknown => console.debug({skillsUnknown})),
        shareReplay()
    );

    private readonly talents$$ = new Subject<Talent[]>();
    public readonly talents$ = this.talents$$.asObservable().pipe(
        tap(talents => console.debug({talents})),
        shareReplay()
    );

    public readonly talentsUnknown$ = combineLatest([
        this.settingService.talents$,
        this.talents$
    ]).pipe(
        map(([allTalents, learnedTalents]) => {
            const learnedTalentIds = new Set(learnedTalents.map(talent => talent.id));
            return allTalents.filter(talent => !learnedTalentIds.has(talent.id));
        }),
        shareReplay(),
        tap(talentsUnknown => console.debug({talentsUnknown}))
    );

    public readonly skillPointsSpent$ = combineLatest([
        this.skillLevels$,
        this.talents$
    ]).pipe(
        map(([skillLevels, talents]) => {
            let spent = 0;
            if(skillLevels.length > 0) {
                spent += skillLevels
                    .map(sl => sl.level)
                    .map(level => this.levelService.getCumulativeSkillLevelCost(level))
                    .reduce((total, value) => total + value);
            }
            if(talents.length > 0) {
                spent += talents.map(t => t.cost)
                    .reduce((total, value) => total + value);
            }
            return spent;
        }),
        tap(skillPointsSpent => console.debug({skillPointsSpent})),
        shareReplay()
    );

    public readonly character$: Observable<Character> = combineLatest([
        this.level$,
        this.primaryAttributeLevels$,
        this.secondaryAttributeLevels$,
        this.skillLevels$,
        this.talents$
    ]).pipe(
        map(([
                 level,
                 primaryAttributeLevels,
                 secondaryAttributeLevels,
                 skillLevels,
                 talents
             ]) => {
            return new Character(
                level,
                primaryAttributeLevels,
                secondaryAttributeLevels,
                skillLevels,
                talents
            );
        }),
        tap(character => console.debug({character})),
        shareReplay()
    );

    private readonly subscriptions = new Subscription();

    private setting!: Setting;
    private character!: Character;
    private skillsLearnable!: number;

    constructor(
        private readonly settingService: SettingService,
        private readonly levelService: LevelService
    ) {
        this.subscriptions.add(
            this.settingService.setting$.pipe(first())
                .subscribe(setting => {
                    this.setting = setting;
                    this.createCharacter();
                })
        );
        this.subscriptions.add(this.character$
            .subscribe(character => this.character = character));
        this.subscriptions.add(this.skillsLearnable$
            .subscribe(skillsLearnable =>
                this.skillsLearnable = skillsLearnable)
        );
    }

    private createCharacter() {
        const primaryAttributeLevels = this.setting.attributes.primary
            .map(attribute => new PrimaryAttributeLevel(attribute, 10));
        this.level$$.next(1);
        this.primaryAttributeLevels$$.next(primaryAttributeLevels);
        this.skillLevels$$.next([]);
        this.talents$$.next([]);
    }

    public updateCharacterLevel(level: number) {
        if (level < 0 || level > 20) return;
        this.level$$.next(level);
    }

    public updatePrimaryAttributeLevel(update: PrimaryAttributeLevel) {
        const level = update.level;
        const id = update.attribute.id;
        console.info("update primary attribute", {id, level});
        if (level < 0 || level > 20) return;
        const primaryAttributeLevels = this.character.primaryAttributeLevels;
        const index = primaryAttributeLevels.findIndex(attributeLevel => attributeLevel.attribute.id === id);
        primaryAttributeLevels[index] = update;
        this.primaryAttributeLevels$$.next(primaryAttributeLevels);
    }

    public learnSkill(skill: Skill) {
        console.info("learn skill", skill.id);
        if (this.character.skillLevels.length >= this.skillsLearnable) return;
        const skillLevel = new SkillLevel(skill, 0);
        const skillLevels = [...this.character.skillLevels, skillLevel];
        this.skillLevels$$.next(skillLevels);
    }

    public unlearnSkill(skill: Skill) {
        const id = skill.id;
        console.info("unlearn skill", id);
        const skillLevels = this.character.skillLevels.filter(sl => sl.skill.id !== id);
        this.skillLevels$$.next(skillLevels);
    }

    public updateSkillLevel(update: SkillLevel) {
        const level = update.level;
        const id = update.skill.id;
        console.info("update skill level", {id, level});
        if (level < 0 || level > 7) return;
        const index = this.character.skillLevels.findIndex(sl => sl.skill.id === id);
        const skillLevels = this.character.skillLevels;
        skillLevels[index] = update;
        this.skillLevels$$.next(skillLevels);
    }

    public learnTalent(talent: Talent) {
        console.info("learn talent", talent.id);
        const talents = [...this.character.talents, talent];
        this.talents$$.next(talents);
    }

    public unlearnTalent(talent: Talent) {
        const id = talent.id;
        console.info("unlearn talent", id);
        const talents = this.character.talents.filter(t => t.id !== id);
        this.talents$$.next(talents);
    }

    private calculateSecondaryAttributeLevels(
        secondaryAttributes: SecondaryAttribute[],
        primaryAttributeLevels: PrimaryAttributeLevel[])
        : SecondaryAttributeLevel[] {
        return secondaryAttributes.map(attribute => {
            const dependencies: PrimaryAttributeLevel[] = attribute.dependencies.map(dependency =>
                primaryAttributeLevels.find(attributeLevel => dependency.id === attributeLevel.attribute.id)!
            );
            const level = dependencies.map(attributeLevel => attributeLevel.level)
                    .reduce((total, value) => total += value)
                / dependencies.length;
            return new SecondaryAttributeLevel(attribute, level);
        });
    }
}
