import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {SettingService} from "../../services/setting.service";
import {map, tap} from "rxjs/operators";
import Talent from "../../model/setting/talent";
import {CharacterService} from "../../services/character.service";

@Component({
    selector: 'newera-talent-editor',
    templateUrl: './talent-editor.component.html',
    styleUrls: ['./talent-editor.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class TalentEditorComponent implements OnInit {

    public readonly talentsLearned$ = this.characterService.talents$;

    public readonly talentsAvailable$ = this.characterService.talentsUnknown$.pipe(
        map(talents => talents.sort((talent1, talent2) =>
            talent1.name.localeCompare(talent2.name)))
    );

    constructor(
        private readonly settingService: SettingService,
        private readonly characterService: CharacterService
    ) {
    }

    ngOnInit(): void {
    }


    public learnTalent(talent: Talent) {
        this.characterService.learnTalent(talent);
    }

    public unlearnTalent(talent: Talent) {
        this.characterService.unlearnTalent(talent);
    }
}
