import {Component, OnDestroy, ViewEncapsulation} from '@angular/core';
import {CharacterService} from "../../services/character.service";

@Component({
    selector: 'newera-level-input',
    templateUrl: './level-input.component.html',
    styleUrls: ['./level-input.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class LevelInputComponent implements OnDestroy {

    public level!: number;
    private readonly subscription;

    constructor(private readonly characterService: CharacterService) {
        ;this.subscription = this.characterService.level$
            .subscribe(level => this.level = level);
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    incrementLevel() {
        this.characterService.updateCharacterLevel(this.level + 1);
    }

    decrementLevel() {
        this.characterService.updateCharacterLevel(this.level - 1);
    }
}
