import {Injectable} from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class LevelService {

    private readonly attributeLevelCost: number[];
    private readonly cumulativeAttributeLevelCost: number[];

    private readonly skillLevelCost: number[];
    private readonly cumulativeSkillLevelCost: number[];

    constructor() {
        this.skillLevelCost = [0, 1, 2, 3, 4, 6, 8, 10];
        this.cumulativeSkillLevelCost = cumulative(this.skillLevelCost);
        const attributeLevelCost = [1, 2, 3, 4, 6, 8, 10, 13, 16, 20];
        const cumulativeAttributeLevelCost = cumulative(attributeLevelCost);
        this.attributeLevelCost = mirrored(attributeLevelCost);
        this.cumulativeAttributeLevelCost = mirrored(cumulativeAttributeLevelCost);
        console.info(this)
    }

    public getCumulativeAttributeLevelCost(level: number) {
        return this.cumulativeAttributeLevelCost[level];
    }

    public getSkillsPointsAvailable(characterLevel: number, insightLevel: number) {
        return 10 + insightLevel + insightLevel * characterLevel;
    }

    public getCumulativeSkillLevelCost(level: number) {
        return this.cumulativeSkillLevelCost[level];
    }

    public getAvailableAttributePoints(level: number, heroic: boolean) {
        if (level <= 0) return 0;
        const byLevel = (level * (level + 1)) / 2
        if (heroic) {
            return byLevel + 4;
        } else {
            return byLevel;
        }
    }
}

function cumulative(values: number[]): number[] {
    const cumulative: number[] = [];
    let total = 0;
    values.forEach(value => {
        total += value;
        cumulative.push(total);
    });
    return cumulative;
}

function mirrored(values: number[], center: number = 0): number[] {
    const mirrored: number[] = [];
    const copy = [...values];
    copy.reverse();
    mirrored.push(...copy.map(v => -v));
    mirrored.push(center);
    copy.reverse();
    mirrored.push(...copy);
    return mirrored;
}
