import {Component} from '@angular/core';
import {CharacterService} from "./services/character.service";
import {map} from "rxjs/operators";
import Talent from "./model/setting/talent";

@Component({
    selector: 'newera-character-editor',
    templateUrl: 'character-editor.component.html',
    styleUrls: ['character-editor.component.scss']
})
export class CharacterEditorComponent {

    public readonly character$ = this.characterService.character$;
    public readonly characterJSON = this.character$.pipe(
        map(character => JSON.stringify(character, null, 2))
    );

    constructor(private readonly characterService: CharacterService) {
    }
}
