import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import PrimaryAttributeLevel from "../../../../model/character/primary-attribute-level";

@Component({
    selector: 'newera-primary-attribute-input',
    templateUrl: './primary-attribute-input.component.html',
    styleUrls: ['./primary-attribute-input.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class PrimaryAttributeInputComponent implements OnInit {

    @Input()
    public attributeLevel!: PrimaryAttributeLevel;

    @Output()
    public attributeLevelUpdated = new EventEmitter<PrimaryAttributeLevel>();

    constructor() {
    }

    ngOnInit(): void {
    }

    incrementLevel() {
        const update = new PrimaryAttributeLevel(
            this.attributeLevel.attribute,
            this.attributeLevel.level + 1
        );
        this.attributeLevelUpdated.emit(update);
    }

    decrementLevel() {
        const update = new PrimaryAttributeLevel(
            this.attributeLevel.attribute,
            this.attributeLevel.level - 1
        );
        this.attributeLevelUpdated.emit(update);
    }
}
