import {Component, Input, OnDestroy, OnInit, Output, ViewEncapsulation, EventEmitter} from '@angular/core';
import {CharacterService} from "../../../../services/character.service";
import Talent from "../../../../model/setting/talent";
import {combineLatest, Observable} from "rxjs";
import {map, shareReplay} from "rxjs/operators";

@Component({
    selector: 'newera-talent-input',
    templateUrl: './talent-input.component.html',
    styleUrls: ['./talent-input.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class TalentInputComponent {

    @Input()
    public talent!: Talent;

    @Input()
    public learned!: boolean;

    @Output()
    public select = new EventEmitter<void>();

    public disabled$: Observable<boolean> = combineLatest([
        this.characterService.primaryAttributeLevels$,
        this.characterService.secondaryAttributeLevels$,
        this.characterService.skillLevels$,
        this.characterService.talents$
    ]).pipe(
        map(([
                 primaryAttributeLevels,
                 secondaryAttributeLevels,
                 skillLevels,
                 talents
             ]) => {
            const requirements = this.talent.requirements;

            const allAttributes = [...primaryAttributeLevels, ...secondaryAttributeLevels];
            const attributesMissing: boolean =
                requirements.attributes.length > 0
                    ? requirements.attributes
                        .map(r => allAttributes.find(a => a.attribute.id === r.id)!.level < r.level)
                        .some(missing => missing)
                    : false;

            const skillsMissing: boolean =
                requirements.skills.length > 0
                    ? requirements.skills
                        .map(r => {
                            const sl = skillLevels.find(s => s.skill.id === r.id);
                            return sl == undefined ? true : sl.level < r.level;
                        })
                        .some(missing => missing)
                    : false;

            const talentsMissing: boolean =
                requirements.talents.length > 0
                    ? requirements.talents
                        .map(r => talents.find(t => t.id === r.id) == undefined)
                        .some(missing => missing)
                    : false;

            console.debug({attributesMissing, skillsMissing, talentsMissing})
            return attributesMissing || skillsMissing || talentsMissing;
        }),
        shareReplay()
    )

    constructor(
        private readonly characterService: CharacterService
    ) {
    }

}
