import {Component, ViewEncapsulation} from '@angular/core';
import {CharacterService} from "../../services/character.service";
import PrimaryAttributeLevel from "../../model/character/primary-attribute-level";

@Component({
    selector: 'newera-attribute-editor',
    templateUrl: './attribute-editor.component.html',
    styleUrls: ['./attribute-editor.component.scss'],
    encapsulation: ViewEncapsulation.ShadowDom
})
export class AttributeEditorComponent {

    public readonly primaryAttributeLevels$ = this.characterService.primaryAttributeLevels$;
    public readonly secondaryAttributeLevels$ = this.characterService.secondaryAttributeLevels$;
    public readonly attributePointsAvailable$ = this.characterService.attributePointsAvailable$;
    public readonly attributePointsSpent$ = this.characterService.attributePointsSpent$;

    constructor(private readonly characterService: CharacterService) {}

    public updatePrimaryAttributeLevel(attributeLevel: PrimaryAttributeLevel) {
        this.characterService.updatePrimaryAttributeLevel(attributeLevel);
    }
}
