import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import Setting from "../model/setting/setting";
import {Observable, ReplaySubject} from "rxjs";
import {map} from "rxjs/operators";
import Concept from "../model/setting/concept";

@Injectable({
    providedIn: 'root'
})
export class SettingService {

    private readonly setting$$ = new ReplaySubject<Setting>(1);
    public readonly setting$ = this.setting$$.asObservable();

    public readonly primaryAttributes$ = this.setting$.pipe(
        map(setting => setting.attributes.primary)
    );
    public readonly primaryAttributesById$ = this.primaryAttributes$.pipe(map(mapById));

    public readonly secondaryAttributes$ = this.setting$.pipe(
        map(setting => setting.attributes.secondary)
    );
    public readonly secondaryAttributesById$ = this.secondaryAttributes$.pipe(map(mapById));

    public readonly skills$ = this.setting$.pipe(
        map(setting => setting.skills)
    );
    public readonly skillsById$ = this.skills$.pipe(map(mapById));

    public readonly talents$ = this.setting$.pipe(
        map(setting => setting.talents)
    );

    public readonly equipment$ = this.setting$.pipe(
        map(setting => setting.items.equipment)
    );

    constructor(http: HttpClient) {
        http.get<Setting>(`http://127.0.0.1:8080/setting.json`)
            .subscribe(this.setting$$);
    }
}

function mapById<T extends Concept>(list: T[]): Map<string, T> {
    const map = new Map<string, T>();
    list.forEach(entry => map.set(entry.id, entry));
    return map;
}
